import { Component, OnInit, Output, EventEmitter, OnDestroy, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Subscription } from 'rxjs';
import { HomeService, UserService } from "@core/services/serviceIndex";

@Component({
  selector: 'app-create-payment',
  templateUrl: './create-payment.component.html',
  styleUrls: ['./create-payment.component.css']
})
export class CreatePaymentComponent implements OnInit, OnDestroy {
  public subscription: Subscription;
  public paymentForm: FormGroup;
  public accTypeList: string[] = ['KBZ', 'AYA', 'CB', 'Yoma'];
  public payTypeList: string[] = ['Income', 'Outcome'];
  public statusList: string[] = ['Pending', 'Approved'];
  public submitted = false;
  public isModal: boolean = false;
  public limit: any = 10;
  public page: any = 1;
  public isAsc: boolean = true;
  public userLists: any = {};
  public searchData: any;
  public trainerList: any = [];
  public accountLists: any = [];
  public isShowTraner: boolean = false;
  public isAccList: boolean = false;

  @Input() editData: any;
  @Output() onSave: any = new EventEmitter<any>();

  constructor(private formBuilder: FormBuilder, private _service: HomeService, private _userService: UserService) { }

  ngOnInit(): void {
  
    this.paymentForm = this.formBuilder.group({
      amount: ["", [Validators.required]],
      currency: ["MMK", [Validators.required]],
      description: ["", [Validators.required]],
      paytype: ["", [Validators.required]],
      status: ["", [Validators.required]],

    }
    );
    if (this.editData)
      this.getDetail();
      else   this.isModal = true;

  }

  getAccountList() {
    this.isModal = false;
    this.subscription = this._service.paymentLists(null, 1, 100, 'createDate', 'asc', 'accounts')
      .subscribe((res) => {
        this.accountLists = res.accounts;
        this.isAccList = true;
        setTimeout(() => {
          this.isModal = true;
        }, 100);

      })
  }
  getDetail() {
    console.log(this.editData);
    this.selectedAcc=this.editData.account;
    this.paymentForm = this.formBuilder.group({
      amount: [this.editData.amount, [Validators.required]],
      description: [this.editData.description, [Validators.required]],
      currency: [this.editData.currency, [Validators.required]],
      paytype: [this.editData.paytype, [Validators.required]],
      status: [this.editData.status, [Validators.required]],
    }
    );
    setTimeout(() => {
      this.isModal = true;
    }, 200);
  }
  onSubmit() {
    console.log(this.paymentForm.invalid);
    this.submitted = true;
    if (this.paymentForm.invalid && this.selectedAcc.accountId) return;
    else {
      this.isModal = false;
      let finalData = this.getFinalData();
      if (this.editData) {
        this.onEdit(finalData);
      }
      else
        this.onCreate(finalData);

    }
  }

  onEdit(data) {
    this.subscription = this._service.editPayment(data, this.editData.paymentId, "payments")
      .subscribe((res) => {
        this.onSave.emit(true);
      })
  }

  onCreate(data) {
    this.subscription = this._service.createPayment(data, "payments")
      .subscribe((res) => {
        this.onSave.emit(true);
      })
  }

  getFinalData() {
    let tempData: any = {};
    let formvalue = this.paymentForm.value;
    tempData.status = formvalue.status;
    tempData.amount = formvalue.amount;
    tempData.currency = formvalue.currency;
    tempData.description = formvalue.description;
    tempData.paytype = formvalue.paytype;
    tempData.accountId = this.selectedAcc.accountId;
    console.log(this.selectedAcc.accountId);
    tempData.payUserId = this.selectedTrainer.userId;
    tempData.payAccountType = this.selectedTrainer.accountType;
    return tempData;

  }

  onCancel() {
    this.onSave.emit(true);
  }



  getTrainerList() {
    this.isShowTraner = true;
    this.isModal = false;
    this.subscription = this._userService.userLists(this.searchData, this.page, this.limit, "createdDate", "asc", "Trainer")
      .subscribe((res) => {
        this.trainerList = res.users;
        setTimeout(() => {
          this.isModal = true;
        }, 100);


      });
  }

  public selectedTrainer: any = {};
  public selectedAcc: any = {};
  onSelectedTrainer(trainer) {
    this.selectedTrainer = trainer;
    this.payTypeList=['Outcome'];
    this.backCreate();
  }

  onSelectedAcc(acc) {
    this.selectedAcc = acc;
    this.isAccList = false;
    this.accountLists = [];
  }
  get f() { return this.paymentForm.controls }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

  backCreate() {
    this.isShowTraner = false;
    this.isAccList = false;
    this.trainerList = [];
    this.accountLists = [];
  }
}

function checkValidate(payAccount: string) {
  return (formGroup: FormGroup) => {
    // const acc = formGroup.controls[account];
    const payacc = formGroup.controls[payAccount];
    if (payacc.value.toString().length < 8) {
      payacc.setErrors({ accountValidate: true });
    } else {
      payacc.setErrors(null);
    }
  }
}
