import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HomeService } from '@app/core/services/home.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-create-fee',
  templateUrl: './create-fee.component.html',
  styleUrls: ['./create-fee.component.css']
})
export class CreateFeeComponent implements OnInit {


  public subscription: Subscription;
  public feeForm: FormGroup;
  public accTypeList: string[] = ['KBZ', 'AYA', 'CB', 'Yoma'];
  public submitted = false;
  public isModal: boolean = false;

  @Input() editData: any;
  @Output() onSave: any = new EventEmitter<any>();

  constructor(private formBuilder: FormBuilder, private _service: HomeService) { }

  ngOnInit(): void {
    this.isModal = true;
    this.feeForm = this.formBuilder.group({
      name: ["", [Validators.required]],
      currency: ["MMK", [Validators.required]],
      amount: [null, [Validators.required]],
      noOfUser: [null, [Validators.required]],
      description: [""],

    }
    );
    if (this.editData)
      this.getDetail();
  }

  getDetail() {
    this.feeForm = this.formBuilder.group({
      name: [this.editData.name, [Validators.required]],
      currency: [this.editData.currency, [Validators.required]],
      amount: [this.editData.amount, [Validators.required]],
      noOfUser: [this.editData.noOfUser, [Validators.required]],
      description: [this.editData.description],
    }
    );
  }
  onSubmit() {
    console.log(this.feeForm.invalid);
    this.submitted = true;
    if (this.feeForm.invalid) return;
    else {
      this.isModal = false;
      let finalData = this.getFinalData();
      if (this.editData) {
        this.onEdit(finalData);
      }
      else
        this.onCreate(finalData);

    }
  }

  onEdit(data) {
    this.subscription = this._service.editPayment(data, this.editData.feeId, "fees")
      .subscribe((res) => {
        this.onSave.emit(true);
      })
  }

  onCreate(data) {
    this.subscription = this._service.createPayment(data, "fees")
      .subscribe((res) => {
        this.onSave.emit(true);
      })
  }

  getFinalData() {
    let tempData: any = {};
    let formvalue = this.feeForm.value;
    tempData.name = formvalue.name;
    tempData.amount = formvalue.amount;
    tempData.currency = formvalue.currency;
    tempData.noOfUser = formvalue.noOfUser;
    tempData.description = formvalue.description;
    return tempData;

  }

  onCancel() {
    this.onSave.emit(true);
  }

  get f() { return this.feeForm.controls }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
